section.dataHistory div.historyType {
  padding: 2px 0;
  text-align: center; }
  section.dataHistory div.historyType span {
    color: white;
    padding: 2px 5px;
    border-radius: 3px; }
  section.dataHistory div.historyType.change span {
    background-color: #a157ae; }
  section.dataHistory div.historyType.remove span {
    background-color: #ff6a6b; }
  section.dataHistory div.historyType.export span {
    background-color: #c88607; }
  section.dataHistory div.historyType.create span {
    background-color: #1cae1a; }

section.dataHistory .link {
  color: #3f51b5; }
  section.dataHistory .link:hover {
    text-decoration: underline; }

section.dataHistory .tags {
  padding: 2px 0;
  text-align: center; }
  section.dataHistory .tags .pill {
    background-color: #3f51b5;
    color: white;
    padding: 2px 6px;
    border-radius: 3px;
    margin-left: 3px; }
    section.dataHistory .tags .pill.export {
      background-color: #c88607; }

.dataHistoryDetails .diff .previous, .dataHistoryDetails .diff .new {
  padding: 8px;
  background-color: #f0f0f0;
  border: 1px solid #d2d2d2;
  border-radius: 3px;
  margin: 0 5px;
  overflow-x: auto; }
  .dataHistoryDetails .diff .previous .changed, .dataHistoryDetails .diff .new .changed {
    background-color: #a157ae;
    color: white; }
  .dataHistoryDetails .diff .previous .row div, .dataHistoryDetails .diff .new .row div {
    padding: 2px 0 2px 5px; }

.dataHistoryDetails h5 {
  margin: 0 0 5px 10px;
  padding: 0; }

.dataHistoryDetails pre {
  padding: 8px;
  background-color: #f0f0f0;
  border: 1px solid #d2d2d2;
  border-radius: 3px;
  margin: 0 5px 0 10px;
  overflow-x: auto; }

md-dialog.dataExport div.log {
  background-color: #e6e6e6;
  display: inline-block;
  color: #323232;
  border: 1px solid #bebebe;
  border-radius: 3px;
  padding: 0 8px;
  max-height: 150px;
  overflow-y: auto; }
