md-content.trackingBoard .stateDefs p {
  margin: 10px 10px 0 0;
  font-size: 11px;
  color: rgba(0, 0, 0, 0.6); }

md-content.trackingBoard .stateDefs p:before {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  border: 1px solid #c8c8c8;
  margin-right: 4px;
  border-radius: 3px; }

md-content.trackingBoard .stateDefs p.complete:before {
  background-color: #2bae57; }

md-content.trackingBoard .stateDefs p.active:before {
  background-color: #65d1cb; }

md-content.trackingBoard .stateDefs p.hold:before {
  background-color: #e9dd4e; }

md-content.trackingBoard .stateDefs p.pending:before {
  background-color: #aaaaaa; }

md-content.trackingBoard .stateDefs p.failed:before {
  background-color: #df2816; }

md-content.trackingBoard .stateDefs p.cancelled:before {
  background-color: #ab1edf; }

md-content.trackingBoard.trackingLane tr.state.complete > td:first-of-type {
  border-left: 4px solid #2bae57; }

md-content.trackingBoard.trackingLane tr.state.active > td:first-of-type {
  border-left: 4px solid #65d1cb; }

md-content.trackingBoard.trackingLane tr.state.hold > td:first-of-type {
  border-left: 4px solid #e9dd4e; }

md-content.trackingBoard.trackingLane tr.state.pending > td:first-of-type {
  border-left: 4px solid #aaaaaa; }

md-content.trackingBoard.trackingLane tr.state.failed > td:first-of-type {
  border-left: 4px solid #df2816; }

md-content.trackingBoard.trackingLane tr.state.cancelled > th:first-of-type {
  border-left: 4px solid #ab1edf; }

md-content.trackingBoard.trackingLane .task {
  border: 1px solid; }
  md-content.trackingBoard.trackingLane .task > .value {
    min-height: 20px;
    padding: 2px 4px; }
  md-content.trackingBoard.trackingLane .task.complete {
    border-left: 5px solid #2bae57;
    background-color: #d3ffd0;
    border-color: #2bae57; }
  md-content.trackingBoard.trackingLane .task.active {
    border-left: 5px solid #65d1cb;
    background-color: #d0f0ff;
    border-color: #65d1cb; }
  md-content.trackingBoard.trackingLane .task.hold {
    border-left: 5px solid #e9dd4e;
    background-color: #f7ffd0;
    border-color: #e9dd4e; }
  md-content.trackingBoard.trackingLane .task.pending {
    border-left: 5px solid #aaaaaa;
    background-color: #ebebeb;
    border-color: #aaaaaa; }
  md-content.trackingBoard.trackingLane .task.failed {
    border-left: 5px solid #df2816;
    background-color: #ffd3c9;
    border-color: #df2816; }
  md-content.trackingBoard.trackingLane .task.cancelled {
    border-left: 5px solid #ab1edf;
    background-color: #ccc1df;
    border-color: #ab1edf; }

md-content.trackingBoard .toast-center-loading {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  padding: 3px 9px;
  margin-bottom: 10px;
  position: absolute;
  left: 46%;
  bottom: 0;
  color: white;
  font-size: 11px; }

md-dialog.lane-checkin h4 {
  margin: 5px 0px 10px 0px; }

md-dialog.ticket_preview md-dialog-content h4 {
  font-size: 12px;
  color: #646464;
  padding: 3px 0;
  margin: 0;
  font-weight: 100; }

md-dialog.ticket_preview md-dialog-content h1 {
  font-size: 20px;
  color: black;
  padding: 0;
  margin: 3px 0 15px 0; }

md-dialog.ticket_preview md-dialog-content h2 {
  font-size: 16px;
  margin: 7px 0;
  padding: 0; }

md-dialog.ticket_preview md-dialog-content table {
  border-collapse: collapse;
  border: 1px solid #cccccc; }
  md-dialog.ticket_preview md-dialog-content table th {
    background-color: whitesmoke;
    font-weight: 700;
    text-align: center;
    font-size: 0.9em; }
  md-dialog.ticket_preview md-dialog-content table th, md-dialog.ticket_preview md-dialog-content table td {
    border: 1px solid #cccccc;
    padding: 3px; }

md-dialog.ticket_preview md-dialog-content div.panel {
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin: 15px 0; }
  md-dialog.ticket_preview md-dialog-content div.panel .title {
    background-color: whitesmoke;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    font-weight: 700;
    padding: 6px;
    font-size: 0.9em;
    border-bottom: 1px solid #cccccc; }
  md-dialog.ticket_preview md-dialog-content div.panel .body {
    padding: 4px; }
    md-dialog.ticket_preview md-dialog-content div.panel .body pre {
      font-family: Roboto,"Helvetica Neue",sans-serif; }
