md-content.reportSection {
  font-size: 12px; }
  md-content.reportSection h2 {
    font-weight: 700;
    margin: 0px 0px 5px 0px;
    padding: 4px 2px 3px 5px;
    background-color: #bcbddc; }
  md-content.reportSection h3 {
    margin: 0px 0px 5px 0px;
    padding: 4px 2px 3px 5px;
    background-color: #efdaf4; }
  md-content.reportSection div.container {
    margin-bottom: 10px; }
    md-content.reportSection div.container div.header {
      padding: 2px;
      background-color: #efedf5;
      border: 1px solid #bcbddc; }
      md-content.reportSection div.container div.header span {
        font-weight: 700; }
      md-content.reportSection div.container div.header div {
        padding: 5px 4px; }
    md-content.reportSection div.container table {
      width: 100%; }
      md-content.reportSection div.container table thead tr th {
        text-align: left;
        padding: 2px 0; }
      md-content.reportSection div.container table tbody tr:nth-child(even) td {
        background-color: #d9d9ea; }
      md-content.reportSection div.container table tbody tr td {
        padding: 2px 2px; }

md-dialog-content.alterationsForm md-autocomplete {
  box-shadow: none; }
  md-dialog-content.alterationsForm md-autocomplete md-autocomplete-wrap {
    box-shadow: none; }

md-dialog-content.alterationsForm md-input-container {
  margin: 14px 0; }

md-dialog-content.alterationsForm .hint {
  font-size: 0.75em;
  color: #828282;
  padding: 4px;
  display: inline-block; }

md-dialog-content.alterationsForm .kbEntryStatus {
  margin: 25px 6px 0 0; }
  md-dialog-content.alterationsForm .kbEntryStatus .valid {
    color: #2ba714; }
  md-dialog-content.alterationsForm .kbEntryStatus .invalid {
    color: #d52c2f; }

md-dialog.alterations md-dialog-actions button.md-button {
  line-height: 18px;
  text-transform: none; }
