.expressionDetails {
  margin-top: 20px; }
  .expressionDetails div {
    font-size: 0.80em;
    text-align: center; }
    .expressionDetails div span {
      font-size: 0.9em;
      font-weight: 700;
      display: block;
      width: 50%;
      margin: 10px auto;
      padding: 8px 15px; }

.expression.material-section .analysis-report-section .comparators {
  border: 1px solid #d2d2d2;
  margin: 15px; }
  .expression.material-section .analysis-report-section .comparators > div {
    border-bottom: none; }
  .expression.material-section .analysis-report-section .comparators > div > div:first-of-type {
    padding: 7px;
    background-color: #e1e1e1;
    border-right: 1px solid #d2d2d2;
    border-bottom: 1px solid #d2d2d2; }
  .expression.material-section .analysis-report-section .comparators > div:last-of-type > div {
    border-right: none; }
    .expression.material-section .analysis-report-section .comparators > div:last-of-type > div > div:last-of-type {
      border-right: none; }
  .expression.material-section .analysis-report-section .comparators > div div:nth-of-type(2) > div {
    background-color: #eeeeee; }
  .expression.material-section .analysis-report-section .comparators > div div:not(first-of-type) > div {
    border-right: 1px solid #d2d2d2;
    border-bottom: 1px solid #d2d2d2;
    padding: 7px;
    text-align: center; }
  .expression.material-section .analysis-report-section .comparators > div > div:last-of-type {
    border-bottom: none; }
    .expression.material-section .analysis-report-section .comparators > div > div:last-of-type > div {
      border-bottom: none; }

.density-graphs ul {
  list-style: none; }
  .density-graphs ul li {
    padding: 10px 0 0 10px; }
    .density-graphs ul li:hover {
      background-color: #eeeeee; }

.density-graphs md-grid-list {
  margin: 15px 0; }
  .density-graphs md-grid-list md-grid-tile {
    margin-bottom: 10px; }
