.report-section .testInformation {
  font-size: 9pt;
  margin: 2mm 0; }
  .report-section .testInformation .title {
    border-bottom: 1px solid #000;
    font-weight: 700;
    font-size: 9pt;
    color: #000;
    padding: 1mm 0;
    text-align: left; }
    .report-section .testInformation .title:first-of-type {
      margin-right: 2mm; }
  .report-section .testInformation .data {
    padding: 1mm 0; }
    .report-section .testInformation .data div:first-of-type {
      font-weight: 100;
      color: #3c3c3c; }
    .report-section .testInformation .data div:last-of-type {
      font-weight: 700;
      color: black; }

.report-section .signature {
  margin-top: 2mm; }
  .report-section .signature div:nth-of-type(2) {
    border: 1px solid black;
    padding: 1mm;
    font-size: 11pt; }
