md-dialog.detailedDataViewer .content {
  padding: 8px;
  background-color: #f0f0f0;
  border: 1px solid #d2d2d2;
  border-radius: 3px;
  margin: 0 5px;
  overflow-x: auto;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace; }
  md-dialog.detailedDataViewer .content div {
    padding: 2px;
    font-size: 0.85em; }
    md-dialog.detailedDataViewer .content div.key {
      padding-right: 10px; }
    md-dialog.detailedDataViewer .content div.null {
      font-style: italic; }
    md-dialog.detailedDataViewer .content div.row:nth-of-type(even) {
      background-color: #e1e1e1; }
