.bread-crumbs {
  background-color: #dcdcdc;
  color: #636363;
  margin: 0;
  padding: 10px 10px 10px 15px;
  border-bottom: 3px solid #cfcfcf; }
  .bread-crumbs .md-subheader-inner {
    margin: 0;
    padding: 0 !important; }
    .bread-crumbs .md-subheader-inner ol {
      list-style-type: none;
      clear: both;
      margin: 0;
      padding: 0; }
      .bread-crumbs .md-subheader-inner ol li {
        float: left;
        padding: 0 0 0 5px; }
        .bread-crumbs .md-subheader-inner ol li span {
          padding-right: 5px; }
        .bread-crumbs .md-subheader-inner ol li a {
          color: #636363;
          text-decoration: none; }
        .bread-crumbs .md-subheader-inner ol li a:hover {
          text-decoration: underline;
          color: #3f51b5; }

.sidebar {
  background-color: #374891;
  color: white;
  padding: 10px 0;
  transition: width 0.5s ease !important;
  border-right: 3px solid #333973; }
  .sidebar div {
    cursor: pointer;
    padding: 5px 10px 5px 10px;
    transition: all 0.2s; }
    .sidebar div.submenu {
      font-size: 0.8em;
      padding-left: 17px; }
      .sidebar div.submenu.active {
        background-color: #e1e1e1; }
      .sidebar div.submenu span {
        padding-left: 4px; }
      .sidebar div.submenu md-icon {
        width: 19px;
        height: 19px;
        min-height: 19px;
        min-width: 19px;
        font-size: 17px; }
    .sidebar div.active {
      background-color: #f0f0f0;
      border-right: 3px solid #c8c8c8;
      color: black;
      position: relative;
      width: calc(100% + 3px); }
      .sidebar div.active md-icon {
        color: #3b4c9b; }
      .sidebar div.active:hover {
        background-color: #d6f3ff; }
    .sidebar div md-icon {
      color: rgba(0, 0, 0, 0.6);
      margin: 0;
      transition: all 0.2s; }
    .sidebar div span {
      padding-left: 6px;
      padding-right: 10px;
      padding-top: 2px; }
    .sidebar div:hover {
      background-color: #485ae0; }
      .sidebar div:hover md-icon {
        color: rgba(20, 20, 20, 0.6); }
    .sidebar div.minimize {
      cursor: pointer;
      outline: none; }
      .sidebar div.minimize md-icon {
        transition: all 0s; }
      .sidebar div.minimize span {
        color: rgba(0, 0, 0, 0.6); }

.jiraTicket {
  background: gainsboro;
  padding: 20px; }
  .jiraTicket .icon {
    width: 50px;
    height: 66px;
    background: url("/assets/images/jira.png"); }
  .jiraTicket.small {
    padding: 10px; }
    .jiraTicket.small .link {
      font-size: 1.1em;
      padding: 10px; }
  .jiraTicket .link {
    text-align: center;
    font-size: 1.3em;
    padding-top: 20px; }
    .jiraTicket .link a, .jiraTicket .link a:visited, .jiraTicket .link a:active {
      color: #3f51b5;
      text-decoration: none; }
    .jiraTicket .link a:hover {
      color: #638cff; }

.lightrope {
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  z-index: 1;
  margin: -18px 0 0 -16px;
  padding: 0;
  pointer-events: none;
  width: 100%; }
  .lightrope li {
    position: relative;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
    width: 8px;
    height: 18px;
    border-radius: 50%;
    margin: 10px;
    display: inline-block;
    background: #00f7a5;
    box-shadow: 0px 3px 16px 3px #00f7a5;
    animation-name: flash-1;
    animation-duration: 2s; }
    .lightrope li:nth-child(2n+1) {
      background: cyan;
      box-shadow: 0px 3px 16px 3px rgba(0, 255, 255, 0.5);
      animation-name: flash-2;
      animation-duration: 0.4s; }
    .lightrope li:nth-child(4n+2) {
      background: #f70094;
      box-shadow: 0px 3px 16px 3px #f70094;
      animation-name: flash-3;
      animation-duration: 1.1s; }
    .lightrope li:nth-child(odd) {
      animation-duration: 1.8s; }
    .lightrope li:nth-child(3n+1) {
      animation-duration: 1.4s; }
    .lightrope li:before {
      content: "";
      position: absolute;
      background: #222;
      width: 6px;
      height: 6px;
      border-radius: 3px;
      top: -3px;
      left: 1px; }
    .lightrope li:after {
      content: "";
      top: -9px;
      left: 5px;
      position: absolute;
      width: 32px;
      height: 12px;
      border-bottom: solid #222 2px;
      border-radius: 50%; }
    .lightrope li:last-child:after {
      content: none; }
    .lightrope li:first-child {
      margin-left: -20px; }

@keyframes flash-1 {
  0%, 100% {
    background: #00f7a5;
    box-shadow: 0px 3px 16px 3px #00f7a5; }
  50% {
    background: rgba(0, 247, 165, 0.4);
    box-shadow: 0px 3px 16px 3px rgba(0, 247, 165, 0.2); } }

@keyframes flash-2 {
  0%, 100% {
    background: cyan;
    box-shadow: 0px 3px 16px 3px cyan; }
  50% {
    background: rgba(0, 255, 255, 0.4);
    box-shadow: 0px 3px 16px 3px rgba(0, 255, 255, 0.2); } }

@keyframes flash-3 {
  0%, 100% {
    background: #f70094;
    box-shadow: 0px 3px 16px 3px #f70094; }
  50% {
    background: rgba(247, 0, 148, 0.4);
    box-shadow: 0px 3px 16px 3px rgba(247, 0, 148, 0.2); } }
