section.material-section.discussion .analysis-report-section .discussion {
  margin: 12px;
  border: 1px solid rgba(0, 0, 0, 0.15); }
  section.material-section.discussion .analysis-report-section .discussion:first-of-type {
    margin-top: 20px; }
  section.material-section.discussion .analysis-report-section .discussion h2 md-icon {
    padding-left: 5px;
    min-height: 14px;
    height: 14px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8); }
  section.material-section.discussion .analysis-report-section .discussion h2 span {
    float: right; }
  section.material-section.discussion .analysis-report-section .discussion div.body {
    padding: 20px;
    white-space: pre-line; }

section.material-section.discussion .analysis-report-section .add-discussion {
  margin: 10px 20px 0 20px;
  padding-bottom: 30px; }
  section.material-section.discussion .analysis-report-section .add-discussion textarea {
    border: 1px solid rgba(0, 0, 0, 0.2);
    background-color: rgba(0, 0, 0, 0.03);
    padding: 5px; }
  section.material-section.discussion .analysis-report-section .add-discussion md-input-container {
    margin: 8px 0 4px 0; }

section.material-section.discussion .analysis-report-section .edit-entry {
  margin: 10px; }

section.material-section.discussion .analysis-report-section .edited {
  font-size: 11px;
  color: rgba(0, 0, 0, 0.5);
  font-style: italic;
  margin: 25px 0 0 0; }
