md-content.definitions .editor .title {
  padding-bottom: 5px;
  color: #646464; }

md-content.definitions .editor .column {
  overflow-y: auto; }
  md-content.definitions .editor .column.definitionsList {
    border: 1px solid #b4b4b4;
    background-color: #ebebeb; }
    md-content.definitions .editor .column.definitionsList .def {
      border-bottom: 1px solid #d2d2d2;
      padding: 8px 6px;
      background-color: white; }
      md-content.definitions .editor .column.definitionsList .def md-icon {
        margin-right: 10px;
        cursor: pointer; }
      md-content.definitions .editor .column.definitionsList .def h1 {
        font-size: 16px;
        font-weight: 500;
        margin: 0;
        padding: 3px 0 0 0;
        cursor: pointer;
        outline: none; }
      md-content.definitions .editor .column.definitionsList .def:hover {
        background-color: #e5f2ff; }
      md-content.definitions .editor .column.definitionsList .def.active {
        background-color: #374891;
        color: white; }
        md-content.definitions .editor .column.definitionsList .def.active md-icon {
          color: white; }
  md-content.definitions .editor .column.definitionsForm {
    border: 1px solid #b4b4b4;
    border-left: none; }
    md-content.definitions .editor .column.definitionsForm.empty {
      background-color: #ebebeb; }
      md-content.definitions .editor .column.definitionsForm.empty p {
        font-size: 20px;
        text-align: center; }
      md-content.definitions .editor .column.definitionsForm.empty .md-button {
        font-size: 18px;
        padding: 4px 10px;
        text-transform: none; }
    md-content.definitions .editor .column.definitionsForm > md-content {
      padding-top: 10px; }

md-content.definitions .editor .tasks {
  min-height: 150px;
  border: 1px solid #b4b4b4;
  margin-bottom: 20px;
  background-color: #ebebeb; }
  md-content.definitions .editor .tasks .task {
    border-bottom: 1px solid #d2d2d2;
    padding: 8px 6px;
    background-color: white; }
    md-content.definitions .editor .tasks .task:not(:first-of-type):last-of-type {
      border-bottom: none; }
    md-content.definitions .editor .tasks .task:hover {
      background-color: #e5f2ff; }
    md-content.definitions .editor .tasks .task .int {
      width: 27px;
      text-align: right;
      margin-right: 8px; }
    md-content.definitions .editor .tasks .task md-icon {
      margin-right: 8px;
      cursor: pointer; }
    md-content.definitions .editor .tasks .task h3 {
      font-size: 16px;
      margin: 0;
      padding: 0;
      font-weight: 500;
      cursor: pointer;
      outline: none; }

md-content.definitions .editor .hook-list .hook {
  padding: 10px;
  background-color: #f7f7f7;
  margin-bottom: 10px;
  cursor: pointer; }
  md-content.definitions .editor .hook-list .hook.add {
    background-color: gainsboro; }
    md-content.definitions .editor .hook-list .hook.add h3 {
      color: #5a5a5a; }
  md-content.definitions .editor .hook-list .hook h3 {
    font-size: 14px;
    margin: 0;
    padding: 0; }
    md-content.definitions .editor .hook-list .hook h3 span {
      font-size: 12px;
      font-weight: 400;
      margin-left: 10px; }
  md-content.definitions .editor .hook-list .hook span.subheader {
    font-size: 12px;
    color: #646464;
    margin-right: 5px; }
    md-content.definitions .editor .hook-list .hook span.subheader span {
      margin-right: 0; }
