md-content.assignment h2.subtitle {
  font-size: 16px;
  font-weight: 300;
  margin: 0 0 8px 0; }

md-content.assignment .editor .title {
  padding-bottom: 5px;
  color: #646464; }

md-content.assignment .editor .column {
  overflow-y: auto; }
  md-content.assignment .editor .column.caseList {
    border: 1px solid #b4b4b4;
    background-color: #ebebeb; }
    md-content.assignment .editor .column.caseList ipr-tracking-card {
      border-bottom: 1px solid #c8c8c8;
      margin: 0; }
      md-content.assignment .editor .column.caseList ipr-tracking-card .header {
        background-color: white;
        color: black; }
        md-content.assignment .editor .column.caseList ipr-tracking-card .header:hover {
          background-color: #f0f0f0;
          color: black; }
      md-content.assignment .editor .column.caseList ipr-tracking-card.selected .header {
        background-color: #3f51b5;
        color: white; }
  md-content.assignment .editor .column .assignmentForm {
    border: 1px solid #b4b4b4;
    border-left: none; }
    md-content.assignment .editor .column .assignmentForm.empty {
      background-color: #ebebeb; }
      md-content.assignment .editor .column .assignmentForm.empty p {
        font-size: 20px;
        text-align: center; }
      md-content.assignment .editor .column .assignmentForm.empty .md-button {
        font-size: 18px;
        padding: 4px 10px;
        text-transform: none; }
    md-content.assignment .editor .column .assignmentForm > md-content {
      padding-top: 10px; }
  md-content.assignment .editor .column .teamStatus {
    padding: 10px;
    border: 1px solid #b4b4b4;
    border-left: none;
    border-top: none; }

md-content.assignment .editor form .tasks {
  border: 1px solid #b4b4b4;
  margin-bottom: 20px;
  background-color: #ebebeb; }
  md-content.assignment .editor form .tasks .task {
    border-bottom: 1px solid #d2d2d2;
    padding: 8px 6px;
    background-color: white;
    outline: none; }
    md-content.assignment .editor form .tasks .task:not(:first-of-type):last-of-type {
      border-bottom: none; }
    md-content.assignment .editor form .tasks .task:hover {
      background-color: #e5f2ff; }
    md-content.assignment .editor form .tasks .task.pending {
      border-left: 5px solid #aaaaaa; }
    md-content.assignment .editor form .tasks .task.active {
      border-left: 5px solid #65d1cb; }
    md-content.assignment .editor form .tasks .task.complete {
      border-left: 5px solid #2bae57; }
    md-content.assignment .editor form .tasks .task.hold {
      border-left: 5px solid #e9dd4e; }
    md-content.assignment .editor form .tasks .task.failed {
      border-left: 5px solid #df2816; }
    md-content.assignment .editor form .tasks .task.cancelled {
      border-left: 5px solid #ab1edf; }
    md-content.assignment .editor form .tasks .task.no-status {
      border-left: 5px solid white; }
    md-content.assignment .editor form .tasks .task .int {
      width: 27px;
      text-align: right;
      margin-right: 8px; }
    md-content.assignment .editor form .tasks .task md-icon {
      margin-right: 8px;
      cursor: pointer; }
    md-content.assignment .editor form .tasks .task h3 {
      font-size: 16px;
      margin: 0;
      padding: 0;
      font-weight: 500;
      cursor: pointer;
      outline: none; }
    md-content.assignment .editor form .tasks .task .assignedTo span:first-of-type {
      margin-right: 5px;
      padding-bottom: 3px; }

md-content.assignment .stateDefs p {
  margin: 10px 10px 0 0;
  font-size: 11px;
  color: rgba(0, 0, 0, 0.6); }

md-content.assignment .stateDefs p:before {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  border: 1px solid #c8c8c8;
  margin-right: 4px;
  border-radius: 3px; }

md-content.assignment .stateDefs p.complete:before {
  background-color: #2bae57; }

md-content.assignment .stateDefs p.active:before {
  background-color: #65d1cb; }

md-content.assignment .stateDefs p.hold:before {
  background-color: #e9dd4e; }

md-content.assignment .stateDefs p.pending:before {
  background-color: #aaaaaa; }

md-content.assignment .stateDefs p.failed:before {
  background-color: #df2816; }

md-content.assignment .stateDefs p.cancelled:before {
  background-color: #ab1edf; }

md-content.assignment .teamList {
  margin: 0;
  padding: 0; }
  md-content.assignment .teamList li {
    margin-top: 6px; }
  md-content.assignment .teamList .workload {
    background-color: #cbd8ff;
    border: 1px solid #333973;
    border-radius: 3px; }
    md-content.assignment .teamList .workload > div {
      background-color: #3f51b5;
      display: inline-block;
      color: white;
      padding: 2px 4px; }
    md-content.assignment .teamList .workload.zero > div {
      color: black;
      padding-left: 4px;
      background: none; }

md-content.assignment .variables {
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin-left: 10px; }
  md-content.assignment .variables h1 {
    font-size: 14px;
    font-weight: 100;
    color: white;
    padding: 5px 7px;
    margin: 0;
    background-color: #3f51b5; }
  md-content.assignment .variables ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    max-height: 270px;
    overflow-y: auto; }
    md-content.assignment .variables ul li {
      padding: 2px 4px;
      font-size: 12px; }
      md-content.assignment .variables ul li:hover {
        background-color: #c8c8c8; }
