md-dialog-content.iprBox svg {
  width: 100%;
  height: 300px; }

md-dialog-content.iprBox .dataBox {
  margin: 7px 4px; }
  md-dialog-content.iprBox .dataBox .infoBar {
    background-color: #9aa2cc;
    font-size: 12px;
    padding: 10px; }
    md-dialog-content.iprBox .dataBox .infoBar span:first-of-type {
      font-weight: 200; }
    md-dialog-content.iprBox .dataBox .infoBar span:nth-of-type(2) {
      font-weight: 600;
      margin-left: 10px; }
  md-dialog-content.iprBox .dataBox.noBottomMargin {
    margin-bottom: 0; }
  md-dialog-content.iprBox .dataBox div.box {
    background-color: #b4b4b4; }
    md-dialog-content.iprBox .dataBox div.box div.heading {
      font-weight: 100;
      font-size: 0.8em;
      text-align: center;
      padding: 10px 0;
      background-color: #cccfe2; }
    md-dialog-content.iprBox .dataBox div.box div.content {
      font-weight: 600;
      font-size: 0.9em;
      text-align: center;
      padding: 20px 0;
      background-color: #ebebeb; }
      md-dialog-content.iprBox .dataBox div.box div.content div {
        padding: 0 5px; }
    md-dialog-content.iprBox .dataBox div.box div.row {
      background-color: #ebebeb;
      font-weight: 300;
      padding: 3px 2px 3px 5px;
      font-size: 0.85em; }
      md-dialog-content.iprBox .dataBox div.box div.row div {
        min-height: 20px;
        vertical-align: middle; }
      md-dialog-content.iprBox .dataBox div.box div.row:nth-of-type(odd) {
        background-color: #d7d7d7; }
      md-dialog-content.iprBox .dataBox div.box div.row:hover {
        background-color: #f7ceaa; }
        md-dialog-content.iprBox .dataBox div.box div.row:hover .edit md-icon {
          display: inline; }
