ipr-progressor .container {
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  width: auto;
  display: inline-block; }

ipr-progressor .stage {
  margin-left: 0;
  background: white;
  border-right: 1px solid lightgrey;
  padding: 10px 34px 10px 28px;
  position: relative;
  display: inline-block; }
  ipr-progressor .stage.active {
    background-color: #3f51b5;
    color: white; }
    ipr-progressor .stage.active:after {
      background-color: #3f51b5; }
  ipr-progressor .stage.complete {
    background-color: #e6e6e6;
    color: #646464; }
    ipr-progressor .stage.complete:after {
      background-color: #e6e6e6; }
  ipr-progressor .stage:after {
    content: '';
    display: block;
    position: absolute;
    top: 40%;
    right: -6px;
    width: 10px;
    height: 10px;
    background: white;
    border-right: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    -moz-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    z-index: 500; }
  ipr-progressor .stage:last-of-type:after {
    display: none; }
  ipr-progressor .stage .stageNumber {
    font-size: 1.5em;
    padding-right: 10px; }
    ipr-progressor .stage .stageNumber md-icon {
      color: #2f9029;
      font-weight: 700; }
  ipr-progressor .stage h1 {
    font-size: 0.85em;
    font-weight: 400;
    padding: 0;
    margin: 0; }
  ipr-progressor .stage p {
    padding: 0;
    margin: 0;
    font-size: 0.75em;
    font-weight: 100; }
