ipr-print-diagram .svgViewer svg {
  width: 255mm;
  height: 100mm;
  font-weight: 100; }

ipr-print-diagram div.details {
  font-size: 10pt; }
  ipr-print-diagram div.details .summary {
    background-color: #bac4cd;
    padding: 1.5mm 2mm; }
    ipr-print-diagram div.details .summary span:first-of-type {
      font-weight: 100;
      margin-right: 2mm; }
    ipr-print-diagram div.details .summary span:last-of-type {
      font-weight: 700; }
  ipr-print-diagram div.details .header {
    background-color: #c2e8f2;
    font-weight: 700;
    padding: 1mm 0;
    margin-top: 1mm; }
    ipr-print-diagram div.details .header div {
      padding-left: 0.5mm; }
  ipr-print-diagram div.details .row {
    margin: 1mm 0;
    border-bottom: 1px solid silver; }
    ipr-print-diagram div.details .row div {
      padding: 1mm; }
  ipr-print-diagram div.details .description {
    border-bottom: 1px solid #010101; }
